'use client'

import { useEffect, useState } from 'react'
import { useParams, useRouter } from 'next/navigation'
import Link from 'next/link'
import { Button } from '@/components/ui/button'
import { useToast } from '@/components/ui/use-toast'
import axios from 'axios'

export default function ScanPage() {
  const params = useParams()
  const router = useRouter()
  const { toast } = useToast()
  const [loading, setLoading] = useState(true)
  const [success, setSuccess] = useState(false)
  const [error, setError] = useState<string | null>(null)
  const [locationStatus, setLocationStatus] = useState<string>('Detecting location...')

  useEffect(() => {
    // Get the QR code ID from the URL parameters
    const qrCode = params.id as string
    
    if (!qrCode) {
      setError('Invalid QR code')
      setLoading(false)
      return
    }

    // Function to get geolocation
    const getLocation = () => {
      if (!navigator.geolocation) {
        setLocationStatus('Geolocation is not supported by your browser')
        recordScan(qrCode, null, null)
        return
      }

      setLocationStatus('Getting your location...')
      
      navigator.geolocation.getCurrentPosition(
        (position) => {
          const { latitude, longitude } = position.coords
          setLocationStatus('Location detected')
          recordScan(qrCode, latitude, longitude)
        },
        (error) => {
          console.error('Error getting location:', error)
          setLocationStatus('Unable to get your location')
          recordScan(qrCode, null, null)
        }
      )
    }

    // Function to record the scan with location data
    const recordScan = async (qrCode: string, latitude: number | null, longitude: number | null) => {
      try {
        const response = await axios.post('/api/scan', {
          qrCode,
          latitude: latitude || 0,
          longitude: longitude || 0,
          deviceInfo: `${navigator.platform} - ${navigator.userAgent}`
        })

        if (response.data.success) {
          setSuccess(true)
          toast({
            title: 'Check-in Successful',
            description: 'Your attendance has been recorded.',
          })
        } else {
          setError(response.data.error || 'Failed to record scan')
        }
      } catch (error: any) {
        console.error('API Error:', error)
        setError(error.response?.data?.error || error.message || 'An error occurred while recording your scan')
      } finally {
        setLoading(false)
      }
    }

    // Start the geolocation process
    getLocation()
  }, [params.id, toast])

  return (
    <div className="container mx-auto px-4 py-8">
      <div className="mb-6">
        <Link href="/">
          <Button variant="outline" className="mb-4">
            ‚Üê Back to Home
          </Button>
        </Link>
        <h1 className="text-3xl font-bold">QR Code Scan</h1>
      </div>

      <div className="max-w-md mx-auto">
        {loading ? (
          <div className="text-center p-8 border rounded-lg">
            <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary mx-auto mb-4"></div>
            <p className="text-xl font-medium mb-2">Processing...</p>
            <p className="text-muted-foreground">{locationStatus}</p>
          </div>
        ) : success ? (
          <div className="text-center p-8 border rounded-lg bg-green-50 dark:bg-green-900/20">
            <div className="w-16 h-16 bg-green-100 dark:bg-green-800 rounded-full flex items-center justify-center mx-auto mb-4">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
                className="w-8 h-8 text-green-600 dark:text-green-300"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M5 13l4 4L19 7"
                />
              </svg>
            </div>
            <h2 className="text-2xl font-bold text-green-700 dark:text-green-300 mb-2">
              Check-in Successful!
            </h2>
            <p className="text-green-600 dark:text-green-400 mb-4">
              Your attendance has been recorded successfully.
            </p>
            <div className="flex justify-center">
              <Link href="/">
                <Button>
                  Return to Home
                </Button>
              </Link>
            </div>
          </div>
        ) : (
          <div className="text-center p-8 border rounded-lg bg-red-50 dark:bg-red-900/20">
            <div className="w-16 h-16 bg-red-100 dark:bg-red-800 rounded-full flex items-center justify-center mx-auto mb-4">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
                className="w-8 h-8 text-red-600 dark:text-red-300"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M6 18L18 6M6 6l12 12"
                />
              </svg>
            </div>
            <h2 className="text-2xl font-bold text-red-700 dark:text-red-300 mb-2">
              Error
            </h2>
            <p className="text-red-600 dark:text-red-400 mb-4">
              {error || 'An unexpected error occurred'}
            </p>
            <div className="flex justify-center space-x-4">
              <Button 
                variant="outline" 
                onClick={() => window.location.reload()}
              >
                Try Again
              </Button>
              <Link href="/">
                <Button>
                  Return to Home
                </Button>
              </Link>
            </div>
          </div>
        )}
      </div>
    </div>
  )
}
